@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@700;900&family=Open+Sans:wght@300;400&display=swap');

*{
  margin: 0;
  padding: 0;
}

body{
  background-color: #90A4AA;
}

.homepage-container{
  display: flex;
  flex-direction:column;
  font-family: 'Montserrat', sans-serif;
  text-align: center;
  align-items: center;
}

.title-container{
  display: inline-block;
  max-width:1900px;
  max-height:1060px;
  margin: 0;
  transition: 0.15s;
}

.homepage-container p{
  font-size: 153px;
  text-transform: uppercase;
  color: white;
  margin-top: 210px;
  line-height:123px;
}

.homepage-container h1{
  text-transform: uppercase;
  font-size: 188px;
  line-height: 159px;
}

.homepage-and{
  color: white;
}

.development{
  font-size: 186px;
  letter-spacing: 1px;
}

.menu-container{
  position: fixed;
  top: 0;
  right: 0;
  z-index: 2;
}

.toggler{
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  opacity: 0;
  z-index: 3;
  cursor: pointer;
}

.hamburger{
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items:center;
  z-index: 2;
}

.hamburger>div{
  position: absolute;
  border: none;
  width: 60%;
  height: 5px;
  background-color: white;
  transition: 0.4s;
}

.hamburger>div:before, .hamburger>div:after{
  content: "";
  position: absolute;
  border: none;
  width: 100%;
  height: 5px;
  background-color: white;
  transition: 0.4s;
}

.hamburger>div:before{
  top: -12px;
}

.hamburger>div:after{
  top: 12px;
}

.toggler:checked + .hamburger>div{
  transform: rotate(-135deg);
}

.toggler:checked:hover + .hamburger>div{
  transform: rotate(-225deg);
}

.toggler:checked + .hamburger>div:before, .toggler:checked + .hamburger>div:after{
  transform: rotate(-90deg);
  top:0;
}

.toggler:checked ~ .menu{
  visibility: visible;
}

.toggler:checked ~ .menu > div{
  transform: translateX(-50%) translateY(-50%);
}

.toggler:checked ~ .menu > div > div{
  opacity: 1;
}

.menu{
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  visibility: hidden;
  transition: 0.6s;
}

.menu > div {
  position: absolute;
  width: 2500px;
  height: 2500px;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translateX(150%) translateY(-50%);
  background-color: black;
  transition: .6s;
  display: flex;
  justify-content: center;
  align-items: center;
}

.menu > div > div{
  max-width: 90vw;
  max-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  opacity: 0;
}

.menu > div > div > ul > li{
  list-style: none;
}

.menu > div > div > ul > li > a{
  text-decoration: none;
  font-family: 'Montserrat', sans-serif;
  color: white;
  font-size: 187px;
  text-transform: uppercase;
  display: inline-block;
  transition: 0.3s;
}

.menu > div > div > ul > li > a:hover{
  letter-spacing: 50px;
  text-decoration: line-through;
}

.face{
  height: 500px;
  width: 383px;
  position: absolute;
  right: 48px;
  bottom: 40px;
  z-index: -1;
}